<template>
  <div v-if="readonly" class="image-list readonly">
    <div data-v-75dfac19>
      <ul class="el-upload-list el-upload-list--picture-card">
        <!-- v-for -->
        <li
          :tabindex="index"
          class="el-upload-list__item is-ready"
          v-for="(img, index) in imageList"
          :key="index+'img'"
        >
          <img :src="img.url" :alt="img.name" class="el-upload-list__item-thumbnail">
          <a class="el-upload-list__item-name">
            <i class="el-icon-document"></i>
            {{img.name}}
          </a>
          <span class="el-upload-list__item-actions">
            <span
              class="el-upload-list__item-preview"
              @click="handlePictureCardPreview({url:img.url})"
            >
              <i class="el-icon-zoom-in"></i>
            </span>
          </span>
        </li>
      </ul>
      <!-- v-for end -->
    </div>
    <el-dialog :visible.sync="dialogVisible" class="dialog-view-img">
      <img width="100%" :src="dialogImageUrl" alt>
    </el-dialog>
  </div>
  <!--  -->
  <div v-else class="image-list">
    <el-upload
      :action="action"
      :file-list="imageList"
      :on-preview="handlePictureCardPreview"
      :on-change="handleFileListChange"
      :on-success="handleFileListSuccess"
      :on-remove="handleFileListRemove"
      :before-remove="handleBeforeRemove"
      :on-progress="handleProgress"
      :auto-upload="true"
      :multiple="true"
      name="file"
      list-type="picture-card"
      ref="upload"
    >
      <i class="el-icon-plus"></i>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible" class="dialog-view-img">
      <img width="100%" :src="dialogImageUrl" alt>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ImageList",
  model: {
    prop: "imageList",
    event: "input"
  },
  props: {
    /*
     *:options="{noDelete:true}"
     */
    options: Object,
    imageList: {
      type: Array,
      required: true
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    window.upload = this.$refs.upload;
  },
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      isUploaded: false,
      addList: [],
      /* update需要的数据 */
      uploadedList: [],
      removedList: []
    };
  },
  computed: {
    action() {
      return (this.options && this.options.action) || "";
    }
  },
  methods: {
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleFileListChange(file, fileList) {
      // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
      if (!this.isUploaded) {
        this.addList = [...this.addList, file];
      }
      this.$emit("input", fileList);
    },
    handleFileListSuccess(res) {
      debugger;
      let { isSuccess, data } = res;
      if (isSuccess) {
        this.uploadedList = [...this.uploadedList, data[0]];
      }
    },
    handleFileListRemove(file, fileList) {
      this.$emit("input", fileList);
      this.removedList = [...this.removedList, file];
      console.log("handleFileListRemove", file, fileList);
    },
    handleProgress(event, file, fileList) {
      console.log(event, event.total, event.percent);
    },
    handleBeforeRemove() {
      if (this.options && this.options.noDelete) return false;
      console.log("delete");
    },
    submit() {
      this.isUploaded = true;
      this.$refs.upload.submit();
    },
    abort() {
      this.$refs.upload.abort();
    }
  },
  watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="scss">
.image-list {
  padding: 1rem;
  .el-upload-list {
    position: relative;
    .el-upload-list__item {
      img.el-upload-list__item-thumbnail {
        margin: 0 auto;
        width: auto;
        display: block;
      }
    }
  }
}
</style>
